.farm_pools_card_access {
  display: flex;
  padding: 10px 0;
  .farm_pools_card_access_title {
    color: #7a7f82;
    line-height: 24px;
    font-size: 14px;
    white-space: nowrap;
    display: flex;
    align-items: center;
  }
  .farm_pools_card_access_content {
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    font-weight: bold;
    img {
      width: 24px;
      height: 24px;
    }
    .access-type-text {
      margin: 0 5px 0 6px;
    }
    .tips {
      position: relative;
      img {
        width: 20px;
        height: 20px;
      }
      .tips_content {
        position: absolute;
        z-index: 99;
        bottom: 30px;
        left: 50%;
        transform: translateX(-50%);
        padding: 0 10px;
        margin-top: 0 !important;
        height: 24px;
        font-style: normal;
        font-size: 12px;
        color: #22292f;
        line-height: 24px !important;
        background: #e1dfd9;
        border-radius: 3px;
        white-space: nowrap;
        font-weight: 400;
        &::after {
          content: "";
          display: inline-block;
          position: absolute;
          bottom: -5px;
          left: 50%;
          transform: translateX(-50%);
          width: 0;
          height: 0;
          border-top: 7px solid #e1dfd9;
          border-left: 7px solid transparent;
          border-right: 7px solid transparent;
        }
        display: none;
      }
      &:hover .tips_content {
        display: block;
      }
    }
  }
}
.farm_pools_card_award-nft {
  & > div {
    display: flex;
    align-items: center;
    padding: 8px 0;
    .nft-img {
      width: 48px;
      height: 48px;
    }
  }
  .desc {
    font-size: 12px;
    font-family: Montserrat-Regular, HelveticaNeue, "Helvetica Neue";
    font-weight: 400;
    color: #162040;
    line-height: 20px;
  }
}
.tips-icon {
  width: 22px;
  height: 22px;
  margin-left: 4px;
  cursor: pointer;
}
.farm_index {
  display: flex;
  flex-wrap: wrap;
}
@media #{$desktop} {
  .farm_box {
    min-width: 1180px;
    background-color: #4ed3ff;
    background-image: url(../../assets/image/cloud_bg.png);
    background-repeat: no-repeat;
    background-size: 100% auto;
    padding-top: 194px;
    margin-top: 80px;
    > img {
      width: 800px;
      height: 288px;
      margin: 0 auto;
    }
    > p {
      font-size: 24px;
      font-family: Montserrat-Bold, HelveticaNeue, "Helvetica Neue";
      font-weight: bold;
      color: #ffffff;
      line-height: 40px;
      margin: 20px auto 0;
      text-align: center;
    }
    .farm_box_main {
      min-height: calc(100% - 80px);
    }
    .footer {
      position: relative;
    }
  }
