    position: relative;
    margin: 74px auto 112px;
    width: 1080px;
    &_card {
      position: relative;
      display: inline-block;
      margin-top: 30px;
      padding: 42px 30px 40px;
      width: 335px;
      //height: 476px;
      border-radius: 20px;
      vertical-align: top;
      background: linear-gradient(
        180deg,
        #ffffff 0%,
        rgba(255, 255, 255, 0.5) 100%
      );
      box-shadow: inset -3px 3px 30px 0px #00c57b;
      border: 1px solid #00c57b;
      border-radius: 5em/5em;
      &::before {
        width: 140px;
        height: 140px;
        position: absolute;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        right: 15px;
        top: 260px;
        content: "";
        display: block;
        background-image: url(../../assets/image/heco_bg@2x.png);
      }
      &:nth-child(3n-1) {
        margin: 30px 30px 0;
      }
      &:after {
        content: "";
        position: absolute;
        top: -22px;
        left: 26px;
        width: 82px;
        height: 50px;
        background: url("../icon/farm/WAR-HT@2x.png") no-repeat;
        background-size: 100% 100%;
      }
      .farm_index_card_chainId_content {
        position: absolute;
        top: 0;
        right: 0;
        width: 60px;
        height: 60px;
        border-top-right-radius: 20px;
        overflow: hidden;
      }
      .farm_index_card_chainId_box {
        position: absolute;
        right: 0px;
        top: 0px;
        z-index: 0;
        width: 0;
        height: 0;
        border-top: 62px solid #c5eecb;
        border-bottom: 62px solid transparent;
        border-left: 62px solid transparent;

        .farm_index_card_chainId {
          position: absolute;
          right: 6px;
          bottom: 30px;
          z-index: 1;
          width: 20px !important;
          height: 20px !important;
          background: url("../../assets/icon/HECO@2x.png") no-repeat;
          background-size: 100% 100%;
        }
      }
    }
    &_card_56 {
      box-shadow: inset -3px 3px 30px 0px #efb90b;
      border: 1px solid #efb90b;
      .farm_index_card_chainId_box {
        border-top: 62px solid #ffeeb9;
        border-bottom: 62px solid transparent;
        border-left: 62px solid transparent;
        .farm_index_card_chainId {
          background: url("../../assets/icon/BSC@2x.png") no-repeat;
          background-size: 100% 100%;
        }
      }
      &::before {
        background-image: url(../../assets/image/BSC_bg@2x.png);
      }
    }
    &_card_137 {
      box-shadow: inset -3px 3px 30px 0px #9758ff;
      border: 1px solid #9758ff;
      .farm_index_card_chainId_box {
        border-top: 62px solid #e1cfff;
        border-bottom: 62px solid transparent;
        border-left: 62px solid transparent;
        .farm_index_card_chainId {
          background: url("../../assets/icon/MATIC@2x.png") no-repeat;
          background-size: 100% 100%;
        }
      }
      &::before {
        background-image: url(../../assets/image/polygon_bg@2x.png);
      }
    }
    .PAUL-USDT:after {
      background: url("../icon/farm/PAUL-HT@2x.png") no-repeat;
      background-size: 100% 100%;
    }
    .WAR:after {
      background: url("../icon/farm/WAR@2x.png") no-repeat;
      background-size: 100% 100%;
    }
    .PLUT:after {
      background: url("../icon/farm/PLUT.png") no-repeat;
      background-size: 100% 100%;
    }
    .WAR-BUSD:after {
      background: url("../icon/farm/WAR_BUSD.png") no-repeat;
      background-size: 100% 100%;
    }
    .WAR-USDT:after {
      background: url("../icon/farm/WAR-USDT@2x.png") no-repeat;
      background-size: 100% 100%;
    }
    .xNFT:after {
      background: url("../icon/farm/X_NFT.png") no-repeat;
      background-size: 100% 100%;
    }
    &_coming {
      background: #f2f0eb;
      &:after {
        content: "";
        position: absolute;
        top: -22px;
        left: 26px;
        width: 50px;
        height: 50px;
        background: url("../icon/farm/coming@2x.png") no-repeat;
        background-size: 100% 100%;
      }
      .farm_index_card_value {
        img {
          display: inline-block;
          width: 24px !important;
          height: 24px !important;
        }
      }
    }
    &_card_title {
      display: flex;
      align-items: center;
      margin-bottom: 0;
      font-size: 20px;
      color: #00c57b;
      font-family: Montserrat-Bold, HelveticaNeue, "Helvetica Neue";
      line-height: 26px;
      font-weight: 600;
    }
    .svip {
      display: inline-block;
      margin-left: 4px;
      width: 34px;
      height: 18px;
      background: url("../icon/farm/SVIP@2x.png") no-repeat;
      background-size: 100% 100%;
    }
    &_card_title_56 {
      color: #efb90b;
    }
    &_card_title_137 {
      color: #9758ff;
    }
    &_card_content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 12px;
      border-bottom: 1px solid #b3d7cb;
      .apr,
      .countdown {
        margin-top: 8px;
        margin-bottom: 0;
        display: flex;
        flex-direction: column;
        line-height: 18px;
        font-size: 18px;
        font-family: Montserrat-Bold, HelveticaNeue, "Helvetica Neue";
        font-weight: bold;
        color: #000000;
      }
      .countdown_time {
        display: inline-block;
        padding: 0 6px;
        background: #c5e5c9;
        border-radius: 3px;
      }
      .countdown_time_56 {
        background: #ffeeb9;
      }
      .countdown_time_137 {
        background: #e1cfff;
      }
      .content_name {
        margin: 8px 0;
        font-family: Montserrat-Regular, HelveticaNeue, "Helvetica Neue";
        color: #4f4f4f;
        line-height: 12px;
        font-size: 12px;
        font-weight: normal;
        .tips {
          position: relative;
          margin-left: 2px;
          display: inline-block;
          width: 16px;
          height: 16px;
          vertical-align: sub;
          cursor: pointer;
          svg {
            fill: #c5e5c9;
          }
        }
        .tips_56 {
          svg {
            fill: #ffeeb9;
          }
        }
        .tips_137 {
          svg {
            fill: #e1cfff;
          }
        }
        .tips_content {
          position: absolute;
          bottom: 50%;
          transform: translateY(50%);
