            width: 0;
            height: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid #c5e5c9;
          }
        }
        .tips_content_56 {
          background: #ffeeb9;
          &::after {
            content: "";
            display: inline-block;
            position: absolute;
            left: -5px;
            top: 50%;
            transform: translateY(-50%);
            width: 0;
            height: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid #ffeeb9;
          }
        }
        .tips_content_137 {
          background: #e1cfff;
          &::after {
            content: "";
            display: inline-block;
            position: absolute;
            left: -5px;
            top: 50%;
            transform: translateY(-50%);
            width: 0;
            height: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid #e1cfff;
          }
        }
      }
    }
    &_card_value {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 0;
      font-size: 14px;
      font-family: Montserrat-Regular, HelveticaNeue;
      color: #7a7f82;
      line-height: 28px;
      img {
        display: inline-block;
        height: 22px;
      }
      span {
        font-size: 14px;
        font-family: Montserrat-Regular, HelveticaNeue;
        color: #121212;
        line-height: 28px;
      }
    }
    &_card_getMLP {
      margin-bottom: 0;
      font-size: 14px;
      line-height: 26px;
      font-family: IBMPlexSans;
      font-weight: 600;
      color: #00c57b;

      &:hover {
        color: #00b900;
      }
    }
    &_card_getMLP_56 {
      color: #efb90b;
      &:hover {
        color: #f3c73b;
      }
    }
    &_card_getMLP_137 {
      color: #e1cfff;
      &:hover {
        color: #9b6bea;
      }
    }
    &_card_btn {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 8px;
      width: 100%;
    }
    .deposit_btn,
    .claim_btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 130px;
      height: 40px;
      // line-height: 40px;
      font-size: 14px;
      font-family: HelveticaNeue-Medium, HelveticaNeue;
      font-weight: 500;
      color: #ffffff;
      border: 0;
      text-align: center;
      background: #00c57b;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      border-radius: 14px;
      &:hover {
        background-color: #00b900;
      }
      &.disabled {
        background-color: #b6b9bc;
        &:hover {
          background-color: #b6b9bc;
          color: #ffffff;
        }
      }
    }
    .deposit_btn_56 {
      background: #f0b90b;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      &:hover {
        background-color: #f3c73b;
      }
    }
    .deposit_btn_137 {
      background: #8247e5;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      &:hover {
        background-color: #9b6bea;
      }
    }
    .claim_btn {
      background: #121212;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      border-radius: 14px;
      &:hover {
        background-color: #22292f;
      }
    }
    .disable_btn {
      background: #b6b9bc !important;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1) !important;
      &:hover {
        background: #b6b9bc !important;
        box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1) !important;
      }
    }
    &_card_rewards {
      margin-top: 12px;
      padding: 8px 10px;
      background: #c5e5c9;
      border-radius: 5px;
    }
    &_card_rewards_56 {
      background: #ffeeb9;
    }
    &_card_rewards_137 {
      background: #e1cfff;
    }
  }
  .farm_popup_avaliable {
    margin-top: 0;
    line-height: 24px !important;
  }
  .vip_tip {
    margin: 8px 0 0;
    font-size: 12px;
    font-family: Montserrat-Regular, HelveticaNeue;
    color: #7a7f82;
  }
}
