      border-bottom: 1px solid #b3d7cb;
      .apr,
      .countdown {
        margin-top: 8px;
        margin-bottom: 0;
        display: flex;
        flex-direction: column;
        font-size: 12px;
        font-family: HelveticaNeue-Bold, HelveticaNeue;
        font-weight: bold;
        color: #22292f;
        line-height: 18px;
      }
      .countdown_time {
        display: inline-block;
        padding: 0 6px;
        background: #c5e5c9;
        border-radius: 3px;
      }
      .countdown_time_56 {
        background: #ffeeb9;
      }
      .countdown_time_137 {
        background: #e1cfff;
      }
      .content_name {
        margin: 8px 0;
        font-size: 12px;
        font-family: HelveticaNeue;
        color: #7a7f82;
        line-height: 16px;
        .tips {
          position: relative;
          margin-left: 2px;
          display: inline-block;
          width: 16px;
          height: 16px;
          vertical-align: sub;
          cursor: pointer;
          svg {
            fill: #c5e5c9;
          }
        }
        .tips_56 {
          svg {
            fill: #ffeeb9;
          }
        }
        .tips_137 {
          svg {
            fill: #e1cfff;
          }
        }
        .tips_content {
          position: absolute;
          bottom: 50%;
          transform: translateY(50%);
          left: 22px;
          padding: 0 10px;
          margin-top: 0 !important;
          height: 24px;
          font-style: normal;
          font-size: 12px;
          color: #22292f;
          line-height: 24px !important;
          background: #c5e5c9;
          border-radius: 3px;
          white-space: nowrap;
          &::after {
            content: "";
            display: inline-block;
            position: absolute;
            left: -5px;
            top: 50%;
            transform: translateY(-50%);
            width: 0;
            height: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid #c5e5c9;
          }
        }
        .tips_content_56 {
          background: #ffeeb9;
          &::after {
            content: "";
            display: inline-block;
            position: absolute;
            left: -5px;
            top: 50%;
            transform: translateY(-50%);
            width: 0;
            height: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid #ffeeb9;
          }
        }
        .tips_content_137 {
          background: #e1cfff;
          &::after {
            content: "";
            display: inline-block;
            position: absolute;
            left: -5px;
            top: 50%;
            transform: translateY(-50%);
            width: 0;
            height: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid #e1cfff;
          }
        }
      }
    }
    &_card_value {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 0;
      font-size: 14px;
      font-family: Montserrat-Regular, HelveticaNeue;
      color: #7a7f82;
      line-height: 28px;
      img {
        display: inline-block;
        //width: 36px;
        height: 22px;
      }
      span {
        font-size: 14px;
        font-family: Montserrat-Regular, HelveticaNeue;
        color: #121212;
        line-height: 28px;
      }
    }
    &_card_getMLP {
      margin-bottom: 0;
      font-size: 14px;
      line-height: 26px;
      font-family: IBMPlexSans;
      font-weight: 600;
      color: #0f8c20;
      text-decoration: underline;
      &:hover {
        color: #00b900;
        text-decoration: underline;
      }
    }
    &_card_getMLP_56 {
      color: #f0b90b;
      &:hover {
        color: #f3c73b;
        text-decoration: underline;
      }
    }
    &_card_getMLP_137 {
      color: #8247e5;
      &:hover {
        color: #9b6bea;
        text-decoration: underline;
      }
    }
    &_card_btn {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 8px;
      width: 100%;
    }
    .deposit_btn,
    .claim_btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 130px;
      height: 40px;
      // line-height: 40px;
      font-size: 14px;
      font-family: HelveticaNeue-Medium, HelveticaNeue;
      font-weight: 500;
      color: #ffffff;
      background: #0f8c20;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      border-radius: 14px;
      text-align: center;
      border: 0;
      &:hover {
        background-color: #00b900;
      }
      &.disabled {
        background-color: #b6b9bc;
        &:hover {
          background-color: #b6b9bc;
          color: #ffffff;
        }
      }
    }
    .deposit_btn_56 {
      background: #f0b90b;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      &:hover {
        background-color: #f3c73b;
      }
    }
    .deposit_btn_137 {
      background: #8247e5;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      &:hover {
        background-color: #9b6bea;
      }
    }
    .claim_btn {
      background: #121212;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      border-radius: 14px;
      &:hover {
        background-color: #22292f;
      }
    }
    .disable_btn {
      background: #b6b9bc !important;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1) !important;
      &:hover {
        background: #b6b9bc !important;
        box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1) !important;
      }
    }
    &_card_rewards {
      margin-top: 12px;
      padding: 8px 10px;
      background: #c5e5c9;
      border-radius: 5px;
    }
    &_card_rewards_56 {
      background: #ffeeb9;
    }
    &_card_rewards_137 {
      background: #e1cfff;
    }
  }
  .farm_popup_avaliable {
    margin-top: 0;
    line-height: 24px !important;
  }
  .vip_tip {
    margin: 8px 0 0;
    font-size: 12px;
    font-family: Montserrat-Regular, HelveticaNeue;
    color: #7a7f82;
  }
}
