.pools-type {
  width: 100%;
  // min-height: 800px;
  p {
    margin-bottom: 0;
  }
}
.pools-type_card_box_border {
  box-sizing: border-box;
  &:hover {
    cursor: pointer;
    border: 1px solid #0f8c20;
  }
}
.crown,
.tips,
.crown-highlight {
  display: inline-block;
  width: 16px;
  height: 16px;
}
.crown {
  margin-right: 2px;
  background: url("../icon/home_N02_night@2x.png") no-repeat;
  background-size: 100% 100%;
}
.crown-highlight {
  margin-right: 2px;
  background: url("../icon/home_N01_night@2x.png") no-repeat;
  background-size: 100% 100%;
}
.tips {
  position: relative;
  margin-left: 2px;
  background: url("../icon/06\ iconminorinfo@2x.png") no-repeat;
  background-size: 100% 100%;
  &:hover {
    cursor: pointer;
  }
}
.tips_content {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  padding: 0 10px;
  margin-top: 0 !important;
  height: 24px;
  font-style: normal;
  font-size: 12px;
  color: #22292f;
  line-height: 24px !important;
  background: #e1dfd9;
  border-radius: 3px;
  white-space: nowrap;
  z-index: 1;
  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
    border-top: 7px solid #e1dfd9;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
  }
}
.nft-card-tip-content {
  display: flex;
  img {
    width: 100px;
    height: 100px;
  }
  & > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    h2 {
      font-size: 18px;
      font-family: Montserrat-Bold, HelveticaNeue;
      font-weight: bold;
      color: #121212;
      line-height: 24px;
    }
    p {
      font-size: 12px;
      font-family: Montserrat-Regular, HelveticaNeue;
      font-weight: 400;
      color: #121212;
      line-height: 12px;
    }
    a {
      color: #0f8c20;
      text-decoration: underline;
    }
  }
}

.pools-type_hover_style_43114 {
  &:hover {
    cursor: pointer;
    border: 1px solid #e84142;
  }
}
.pools-type_hover_style_128 {
  &:hover {
    cursor: pointer;
    border: 1px solid #0f8c20;
  }
}
.pools-type_hover_style_56 {
  &:hover {
    cursor: pointer;
    border: 1px solid #f0b90b;
  }
}
.pools-type_hover_style_137 {
  &:hover {
    cursor: pointer;
    border: 1px solid #8247e5;
  }
}

