          min-width: 124px;
          height: 32px;
          border-radius: 8px;
          line-height: 32px;
          border: 2px solid #00c57b;
        }
      }
      .pools_sum {
        position: absolute;
        top: 8px;
        right: -24px;
        padding: 0 10px;
        height: 16px;
        line-height: 16px;
        color: #fff;
        font-size: 14px;
        background: #e44c2c;
        border-radius: 10px;
      }
    }
    &_noData {
      padding-top: 120px;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      img {
        width: 370px;
        height: 200px;
      }
      p {
        margin-top: 16px;
        font-size: 12px;
        font-weight: 400;
        color: #7a7f82;
        font-family: Montserrat-SemiBold, HelveticaNeue;
      }
    }
    .flexStyle {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    &_card {
      display: grid;
      grid-template-columns: repeat(3, 380px);
      place-content: space-between;
      .border_56 {
        border: 1px solid #f7ecc9;
      }
      .border_128 {
        border: 1px solid #ccf3e5;
      }
      .border_137 {
        border: 1px solid #e1d5f5;
      }
      .border_43114 {
        border: 1px solid #f5d4d4;
      }
      .pools-type_card_box {
        margin-top: 30px;
        box-sizing: border-box;
        display: inline-block;
        border-radius: 20px;
        vertical-align: top;
        width: 380px;
        background: #f9f9f9;
        border-radius: 14px;
        .type_private_box_t {
          border-radius: 14px 14px 0 0;
          width: 100%;
          height: 210px;
          background-image: url(../../assets/image/default-pool.png);
          background-repeat: no-repeat;
          background-size: 100% 100%;
        }
        .type_private_box_b {
          height: 240px;
          position: relative;
          &_logo {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 0 0 20px;
            position: absolute;
            top: -52px;

            &_l {
              background: #fff;
              border-radius: 12px;
              > img {
                width: 80px;
                height: 80px;
                border-radius: 12px;
                border: 6px solid #ffffff;
              }
            }
            &_g {
              width: 282px;
              height: 24px;
              background: rgba(255, 255, 255, 0.7);
              text-align: right;
              padding-right: 20px;
              > span {
                height: 16px;
                border-radius: 3px;
                font-size: 12px;
                font-family: PingFangSC-Regular, PingFang SC;
                line-height: 12px;
                padding: 0 3px;
              }
            }
          }
          &_name {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 40px 20px 0;
            > h3 {
              font-size: 20px;
              font-family: Montserrat-Bold, HelveticaNeue;
              font-weight: bold;
              color: #000000;
              line-height: 24px;
              margin-bottom: 0;
              display: flex;
              align-items: center;
            }
          }
          &_text {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 20px;
            margin-top: 10px;
            > span {
              &:nth-of-type(1) {
                font-size: 14px;
                font-family: Montserrat-Regular, HelveticaNeue;
                color: #4f4f4f;
                line-height: 14px;
              }
              &:nth-of-type(2) {
                font-size: 14px;
                font-family: Montserrat-Bold, HelveticaNeue;
                font-weight: bold;
                color: #000000;
                line-height: 18px;
                display: flex;
                align-items: center;
              }
            }
          }
        }
        .lpt_link {
          width: 300px;
          text-align: center;
          font-size: 12px;
          color: #0f8c20;
          &:hover {
            text-decoration: underline;
          }
        }
      }
      @keyframes light {
        from {
          box-shadow: 0px 0px 18px 2px rgba(15, 140, 32, 0.1);
        }
        to {
          box-shadow: 0px 0px 18px 2px rgba(15, 140, 32, 1);
        }
      }
      .pools-type_breathing_light {
        animation: light 1.5s infinite linear alternate forwards 1s;
      }
      .pools-type_private {
        background: #fffbfa;
      }
      .pools-type_flashPool {
        background: url("../icon/shandian@2x.png") no-repeat;
        background-position: right bottom;
        background-size: 110px 150px;
        background-color: #fffaf2;
      }
    }
    &_title {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      img {
        display: inline-block;
        width: 50px;
        height: 50px;
        margin-right: 6px;
      }
      .pools-type_card_title {
        display: flex;
        align-items: center;
        margin-bottom: 0;
        font-size: 16px;
        font-weight: bold;
        color: #22292f;
        line-height: 20px;
      }
      .pools-type_card_title_right {
        display: flex;
        align-items: center;
        flex-direction: column;
      }
      .pools-type_time {
        display: flex;
        align-items: center;
        margin-bottom: 0;
