          display: inline-block;
          padding: 0 4px;
          height: 20px;
          text-align: center;
          background: #f2f0eb;
          border-radius: 3px;
          font-size: 12px;
          font-weight: 600;
          color: #22292f;
          line-height: 20px;
        }
      }
      span {
        display: inline-block;
        padding: 0 4px;
        height: 18px;
        font-size: 12px;
        font-weight: 400;
        color: #22292f;
        line-height: 18px;
        transform: scale(0.9);
        border-radius: 3px;
      }
      .pools-type_over_status {
        color: #42526e;
        background: #dfe1e6;
      }
      .pools-type_progress_status {
        color: #0f8c20;
        background: #d4e9e2;
      }
      .pools-type_coming_status {
        color: #22292f;
        background: #fff0b3;
      }
      .pools-type_card_ratio {
        margin-top: 14px;
        margin-bottom: 0;
        font-size: 14px;
        font-weight: bold;
        color: #22292f;
        line-height: 14px;
        i {
          margin-top: 10px;
          display: block;
          font-style: normal;
          font-size: 12px;
          color: #22292f;
          line-height: 12px;
        }
      }
      .pools-type_card_access {
        margin-top: 20px;
        line-height: 24px;
        display: flex;
        align-items: center;
        .nft-badge {
          & > img {
            width: 48px;
            height: 48px;
          }
        }
      }
    }

    &_chaind_box_128 {
      position: absolute;
      right: -22px;
      top: -30px;
      z-index: 0;
      width: 62px;
      height: 62px;
      background: url("../icon/pool_heco_arrow.svg") no-repeat;
