        width: 20px !important;
        height: 20px !important;
        background: url("../../assets/icon/HECO@2x.png") no-repeat;
        background-size: 100% 100%;
      }
    }
    &_chaind_box_56 {
      position: absolute;
      right: -22px;
      top: -30px;
      z-index: 0;
      width: 62px;
      height: 62px;
      background: url("../icon/pool_bsc_arrow.svg") no-repeat;
      background-size: 100% 100%;
      .pools-type_chaind {
        position: absolute;
        right: 6px;
        bottom: 30px;
        z-index: 1;
        width: 20px !important;
        height: 20px !important;
        background: url("../../assets/icon/BSC@2x.png") no-repeat;
        background-size: 100% 100%;
      }
    }
    &_chaind_box_43114 {
      position: absolute;
      right: -22px;
