        z-index: 1;
        width: 20px !important;
        height: 20px !important;
        background: url("../../assets/icon/avalanche_f.svg") no-repeat;
        background-size: 100% 100%;
      }
    }
    &_chaind_box_137 {
      position: absolute;
      right: -22px;
      top: -30px;
      z-index: 0;
      width: 62px;
      height: 62px;
      background: url("../icon/pool_polygon_arrow.svg") no-repeat;
      background-size: 100% 100%;
      .pools-type_chaind {
        position: absolute;
        right: 6px;
        bottom: 30px;
        z-index: 1;
        width: 20px !important;
        height: 20px !important;
        background: url("../../assets/icon/MATIC@2x.png") no-repeat;
        background-size: 100% 100%;
      }
    }
    &_percentage {
      a {
        margin-top: 10px;
        position: relative;
        display: block;
        width: 100%;
        height: 8px;
        background: #f2f0eb;
        border-radius: 5px;
        .pools-type_progress_bar {
          position: absolute;
          left: 0;
          top: 0;
          height: 8px;
          background: #0f8c20;
          border-radius: 5px;
        }
        .pools-type_progress_bar_56 {
          background: #f0b90b;
        }
        .pools-type_progress_bar_137 {
          background: #8247e5;
        }
      }
      p {
        margin-top: 4px;
        margin-bottom: 0;
        font-size: 12px;
        color: #22292f;
        line-height: 12px;
      }
    }
    &_enter {
      display: inline-block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 48px;
      line-height: 48px;
      font-size: 16px;
      color: #00c57b;
      font-weight: 800;
      background: rgba(0, 197, 123, 0.2);
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      text-align: center;
      border-radius: 0px 0px 14px 14px;
      font-family: Montserrat-ExtraBold, Montserrat;
      &:hover {
        background: rgba(0, 197, 123, 0.5);
        color: #03a678 !important;
      }
    }
    &_bsc_enter {
      background: rgba(240, 185, 11, 0.2);
      color: #f0b90b;
      &:hover {
        background: rgba(240, 185, 11, 0.5);
        color: #cf9f06 !important;
      }
    }
    &_matic_enter {
      background: rgba(130, 71, 229, 0.2);
      color: #8247e5;
      &:hover {
        color: #7835e9 !important;
        background: rgba(130, 71, 229, 0.5);
      }
    }

    &_avalanche_enter {
      background: rgba(232, 65, 66, 0.2);
      color: #e84142;
      &:hover {
        background: rgba(232, 65, 66, 0.5);
        color: #ec292b !important;
      }
    }
    &_disable_enter {
      color: #fff;
      background: #b6b9bc;
      &:hover {
        color: #fff !important;
        background: #b6b9bc;
      }
    }
    &_lbp_enter {
      margin-top: 10px;
    }
    .w_bg {
      display: inline-block;
      margin-top: 0;
      float: right;
      width: 182px;
      height: 100px;
    }
    .svip {
      display: inline-block;
      margin-left: 4px;
      width: 34px;
      height: 18px;
      background: url("../icon/farm/SVIP@2x.png") no-repeat;
      background-size: 100% 100%;
    }
    &_warning {
      display: inline-block;
    }
    .warning_tips {
      display: inline-block;
      width: 24px;
      height: 24px;
      background: url("../icon/error-warning@2x.png") no-repeat;
      background-size: 100% 100%;
      vertical-align: sub;
    }
    .warning_tips_content {
      position: absolute;
      bottom: 30px;
      left: 50%;
      transform: translateX(-50%);
      padding: 8px 10px;
      margin-top: 0 !important;
      width: 288px;
      font-style: normal;
      font-size: 12px;
      color: #22292f;
      line-height: 24px !important;
      background: #e1dfd9;
      border-radius: 3px;
      &::after {
        content: "";
        display: inline-block;
        position: absolute;
        bottom: -5px;
        left: 50%;
        transform: translateX(-50%);
        width: 0;
        height: 0;
        border-top: 7px solid #e1dfd9;
        border-left: 7px solid transparent;
        border-right: 7px solid transparent;
      }
    }
  }
}
