@media #{$desktop} {
  .application_card {
    margin-top: 32px;
    width: 100%;
    height: 150px;
    background: #ffffff;
    box-shadow: 6px 6px 0px 0px #e7f4ef;
    border-radius: 16px;
    border: 4px solid #e7f4ef;
    &_title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 20px;
      width: 100%;
      height: 48px;
      border-bottom: 4px solid #e7f4ef;
      i {
        font-style: normal;
        font-size: 18px;
        font-family: Montserrat-Regular, Montserrat, HelveticaNeue,
          "Helvetica Neue";
        font-weight: 400;
        color: #03a678;
      }
    }
    .application_countdown_box {
      display: flex;
      align-items: center;
      padding: 0 12px;
      margin-right: -4px;
      height: 100%;
      background: #e7f4ef;
      border-radius: 16px 16px 0px 0px;
      .success {
        margin-bottom: 0;
        font-size: 14px;
        font-family: Montserrat-Bold, Montserrat, HelveticaNeue,
          "Helvetica Neue";
        font-weight: bold;
        color: #4f4f4f;
      }
    }

    &_content_h5 {
      display: none;
    }
    &_content {
      display: flex;
      align-items: center;
      padding: 18px 20px;
      .placeholder_map {
        margin: 0;
        width: 60px;
        height: 60px;
        background: #e7f4ef;
        border-radius: 50%;
      }
      img {
        width: 60px;
        height: 60px;
      }
      .application_card_content_title {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 0;
        margin-left: 52px;
        width: 128px;
        font-size: 14px;
        font-family: Montserrat-Regular, Montserrat, HelveticaNeue,
          "Helvetica Neue";
        font-weight: 400;
        color: #959595;
        line-height: 18px;
        span {
          display: inline-block;
          margin-top: 10px;
          font-size: 18px;
          font-family: Montserrat-Bold, Montserrat, HelveticaNeue,
            "Helvetica Neue";
          font-weight: bold;
          color: #4f4f4f;
          line-height: 20px;
        }
      }
      .application_card_content_progress {
        width: 240px;
        a {
          position: relative;
          margin-top: 16px;
          width: 240px;
          height: 10px;
          background: #e7f4ef;
          border-radius: 5px;
        }
        span {
          position: absolute;
          top: 0;
          left: 0;
          margin: 0;
          width: 0;
          height: 10px;
          background: #03a678;
          border-radius: 5px;
        }
      }
      .application_card_content_btn {
        display: flex;
        align-items: center;
        margin-bottom: 0;
        margin-left: 60px;
        a {
          display: inline-block;
          padding: 0 38px;
          height: 40px;
          line-height: 34px;
          font-size: 18px;
          font-family: TestFoundersGrotesk-Semibold, TestFoundersGrotesk,
            HelveticaNeue, "Helvetica Neue";
          font-weight: 600;
          color: #00c57b;
          background: #fff;
          border-radius: 22px;
          border: 3px solid #00c57b;
          &:hover {
            box-shadow: 3px 3px 0px 0px #03a678;
          }
          &:last-child {
            margin-left: 10px;
            font-size: 16px;
            font-family: TestFoundersGrotesk-Semibold, TestFoundersGrotesk,
              HelveticaNeue, "Helvetica Neue";
            font-weight: 600;
            color: #ffffff;
            background: #00c57b;
          }
        }
        .disable_failed {
          color: #d8d8d8;
          border: 3px solid #d8d8d8;
          &:hover {
            box-shadow: none;
          }
        }
      }
    }
  }
  .application_countdown {
    margin: 0;
    .countdown {
      font-size: 14px;
      font-family: Montserrat-Bold, Montserrat, HelveticaNeue, "Helvetica Neue";
      font-weight: bold;
      color: #4f4f4f;
      line-height: 18px;
    }
    .countdown_time {
      display: inline-block;
      width: 22px;
      height: 22px;
      font-size: 14px;
      font-family: Montserrat-SemiBold, Montserrat, HelveticaNeue,
        "Helvetica Neue";
      font-weight: 600;
      color: #4f4f4f;
      line-height: 22px;
      border-radius: 4px;
      text-align: center;
      border: 1px solid #c9d7d1;
    }
    i {
      font-size: 14px;
      font-family: Montserrat-SemiBold, Montserrat, HelveticaNeue,
        "Helvetica Neue";
      font-weight: 600;
      color: #959595;
      line-height: 16px;
    }
  }
}
